<template>
  <app-content-wrapper :pageTitle="pageTitle">
    <ui-form :name="'blogConfigForm'"
             :ref="'blogConfigForm'"
             :class="'blog-config__frm'"
             @onsubmit="onSubmit"
      >
      <ui-hidden-field :name="'id'" :id="'blogConfigId'" :value="blogConfig.id"></ui-hidden-field>

      <ui-write-table :name="'블로그 환경설정 작성 폼'"
                      :colWidth="['20%', '80%']"
      >
        <tr>
          <th scope="row">
            <label for="blogConfigTitle">블로그 제목<br>(100자 이내)
              <sup>*
                <span class="sr-only">필수입력란</span>
              </sup>
            </label>
          </th>
          <td>
            <ui-text-field :type="'text'"
                           :name="'title'"
                           :id="'blogConfigTitle'"
                           :class="'blog-config__title'"
                           :block="true"
                           :rules="'required|max:100'"
                           :value="blogConfig.title"
                           v-model="previewBlogConfig.title">
            </ui-text-field>
          </td>
        </tr>
        <tr>
          <th scope="row">
            <label for="blogConfigAuthor">닉네임(30자 이내)
              <sup>*
                <span class="sr-only">필수입력란</span>
              </sup>
            </label>
          </th>
          <td>
            <ui-text-field :type="'text'"
                           :name="'author'"
                           :id="'blogConfigAuthor'"
                           :class="'blog-config__author'"
                           :block="true"
                           :rules="'required|max:30'"
                           :value="blogConfig.author"
                           v-model="previewBlogConfig.author">
            </ui-text-field>
          </td>
        </tr>
        <tr>
          <th scope="row">
            <label for="blogConfigDesc">블로그 소개(100자 이내)</label>
          </th>
          <td>
            <ui-textarea :name="'desc'"
                         :id="'blogConfigDesc'"
                         :cols="'30'"
                         :rows="'10'"
                         :resize="'vertical'"
                         :rules="'required|max:100'"
                         :value="blogConfig.desc">
            </ui-textarea>
          </td>
        </tr>
        <tr>
          <th scope="row">
            <label for="blogConfigAvatarImgFile">블로그 아바타 이미지</label>
          </th>
          <td>
            <ui-hidden-field :name="'avatarImg'" :id="'blogConfigAvatarImg'" :value="blogConfig.avatarImg"></ui-hidden-field>
            <ui-hidden-field :name="'avatarImgUrl'" :id="'blogConfigAvatarImgUrl'" :value="blogConfig.avatarImgUrl"
                             v-model="previewBlogConfig.avatarImgUrl">
            </ui-hidden-field>
            <ui-hidden-field :name="'avatarImgSize'"
                             :id="'blogConfigAvatarImgSize'"
                             :value="blogConfig.avatarImgSize">
            </ui-hidden-field>

            <ui-file-field :name="'avatarImgFile'"
                           :id="'blogConfigAvatarImgFile'"
                           :accept="'image/*'"
                           :gap="10"
                           @onchange="onChangeAvatarImg">

              <ui-file-button :type="'button'"
                              :color="'secondary'"
                              :listKey="'avatar'"
                              @listFile="onListFile"
                              @clickFile="onClickFile">Cloudinary
              </ui-file-button>
            </ui-file-field>

            <div class="blog-config__avatar-image-use-wrapper" v-if="blogConfig.avatarImg">
              <span class="blog-config__avatar-image-use">
                {{ avatarImg }} (용량 : {{ getFileSize(avatarImgSize) }})
              </span>

              <ui-checkbox :name="'delAvatarImg'"
                           :id="'blogConfigDelAvatarImg'"
                           :label="'삭제'"
                           :values="'Y,N'">
              </ui-checkbox>
            </div>
          </td>
        </tr>
        <tr>
          <th scope="row">
            <label for="blogConfigOgImgFile">블로그 대표 이미지</label>
          </th>
          <td>
            <ui-hidden-field :name="'ogImg'" :id="'blogConfigOgImg'" :value="blogConfig.ogImg"></ui-hidden-field>
            <ui-hidden-field :name="'ogImgUrl'" :id="'blogConfigOgImgUrl'" :value="blogConfig.ogImgUrl"
                             v-model="previewBlogConfig.ogImgUrl">
            </ui-hidden-field>
            <ui-hidden-field :name="'ogImgSize'" :id="'blogConfigOgImgSize'" :value="blogConfig.ogImgSize"></ui-hidden-field>

            <ui-file-field :name="'ogImgFile'"
                           :id="'blogConfigOgImgFile'"
                           :accept="'image/*'"
                           :gap="10"
                           @onchange="onChangeOgImg">

              <ui-file-button :type="'button'"
                              :color="'secondary'"
                              :listKey="'og'"
                              @listFile="onListFile"
                              @clickFile="onClickFile">Cloudinary
              </ui-file-button>
            </ui-file-field>

            <div class="blog-config__og-image-use-wrapper" v-if="blogConfig.ogImg">
              <span class="blog-config__og-image-use">
                {{ ogImg }} (용량 : {{ getFileSize(ogImgSize) }})
              </span>

              <ui-checkbox :name="'delOgImg'"
                           :id="'blogConfigDelOgImg'"
                           :label="'삭제'"
                           :values="'Y,N'">
              </ui-checkbox>
            </div>
          </td>
        </tr>
        <tr>
          <th scope="row">
            <label for="blogConfigOgImgContrast">블로그 대표 이미지 밝기</label>
          </th>
          <td>
            <ui-numeric-field :name="'ogImgContrast'"
                              :id="'blogConfigOgImgContrast'"
                              :step="'0.01'"
                              :min="'0'"
                              :max="'1.1'"
                              :rules="'between:0,1.1'"
                              :text="'0.1 ~ 1 미만 (초기화 0)'"
                              :value="blogConfig.ogImgContrast"
                              v-model="previewBlogConfig.ogImgContrast">
            </ui-numeric-field>
          </td>
        </tr>
        <tr>
          <th scope="row">
            <label for="blogConfigOgImgBlur">블로그 대표 이미지 흐림</label>
          </th>
          <td>
            <ui-numeric-field :name="'ogImgBlur'"
                              :id="'blogConfigOgImgBlur'"
                              :step="'0.01'"
                              :min="'0'"
                              :max="'9.9'"
                              :rules="'between:0,9.9'"
                              :text="'0.1 ~ 10 미만 (초기화 0)'"
                              :value="blogConfig.ogImgBlur"
                              v-model="previewBlogConfig.ogImgBlur">
            </ui-numeric-field>
          </td>
        </tr>
        <tr class="blog-config__og-image-wrapper blog-config__og-image-wrapper--active">
          <th scope="row">블로그 대표 이미지 위치값</th>
          <td>
            <ui-numeric-field :name="'ogImgPosX'"
                              :id="'blogConfigOgImgPosX'"
                              :min="'0'"
                              :max="'100'"
                              :rules="'between:0,100'"
                              :text="'가로 0 ~ 100 (기본 50)'"
                              :title="'블로그 대표 이미지 가로 위치값'"
                              :value="blogConfig.ogImgPosX"
                              v-model="previewBlogConfig.ogImgPosX">
            </ui-numeric-field>

            <ui-numeric-field :name="'ogImgPosY'"
                              :id="'blogConfigOgImgPosY'"
                              :min="'0'"
                              :max="'100'"
                              :rules="'between:0,100'"
                              :text="'세로 0 ~ 100 (기본 50)'"
                              :title="'블로그 대표 이미지 세로 위치값'"
                              :value="blogConfig.ogImgPosY"
                              v-model="previewBlogConfig.ogImgPosY">
            </ui-numeric-field>
          </td>
        </tr>
        <tr>
          <th scope="row">
            <label for="blogConfigPageSize">메인 포스트 목록 <br>출력 개수</label>
          </th>
          <td>
            <ui-numeric-field :name="'pageSize'"
                              :id="'blogConfigPageSize'"
                              :min="'2'"
                              :max="'10'"
                              :rules="'between:2,10'"
                              :text="'2 ~ 10 (기본 6)'"
                              :value="blogConfig.pageSize">
            </ui-numeric-field>
          </td>
        </tr>
        <tr>
          <th scope="row">
            <label for="blogConfigshowSatisYn">페이지 만족도조사 표출</label>
          </th>
          <td>
            <ui-checkbox :name="'showSatisYn'"
                         :id="'blogConfigshowSatisYn'"
                         :label="'페이지 만족도조사 표출'"
                         :labelHidden="true"
                         :values="'Y,N'"
                         v-model="showSatisYn">
            </ui-checkbox>
          </td>
        </tr>

        <template v-slot:btn>
          <ui-button :type="'reset'"
                     :color="'secondary'">다시작성
          </ui-button>

          <ui-button :type="'submit'"
                     :color="'primary'">저장
          </ui-button>
        </template>
      </ui-write-table>
    </ui-form>
  </app-content-wrapper>
</template>

<script>
import { breadcrumbService } from '@/services/breadcrumb/breadcrumbService';
import { messageUtil, getFileSize, isNotEmpty } from '@/utils';

export default {
  name: 'app-blog-config',
  data() {
    return {
      pageTitle: '블로그 환경설정',
      avatarImg: '',
      avatarImgUrl: '',
      avatarImgSize: '',
      ogImg: '',
      ogImgUrl: '',
      ogImgSize: '',
      showSatisYn: '',
      avatarFileList: [],
      ogFileList: [],
      previewBlogConfig: {},
      getFileSize,
    }
  },
  /** 해당 컴포넌트를 벗어나 새로운 페이지로 이동할 때 호출됨 */
  async beforeRouteLeave(to, from, next) {
    if (isNotEmpty(this.$store.state.BlogConfig?.previewData)) {
      //블로그 환경설정 미리보기 데이타 초기화
      this.$store.dispatch('BlogConfig/FETCH_PREVIEW_DATA', null);
      
      await this.$store.dispatch('BlogConfig/GET_BLOG_CONFIG', {
        client: this.$http
      });
    }
    next();
  },
  created() {
    // 페이지 타이틀 세팅
    breadcrumbService.setPageTitle(this.pageTitle);

    this.avatarImg = this.blogConfig.avatarImg;
    this.avatarImgSize = this.blogConfig.avatarImgSize;
    this.ogImg = this.blogConfig.ogImg;
    this.ogImgSize = this.blogConfig.ogImgSize;
    this.showSatisYn = this.blogConfig.showSatisYn;

    this.$store.dispatch('BlogConfig/FETCH_PREVIEW_DATA', null);
    this.previewBlogConfig = Object.assign({}, this.$store.state.BlogConfig.data);
  },
  computed: {
    /** 블로그 환경설정 */
    blogConfig: {
      get() {
        return this.$store.state.BlogConfig.data;
      },
      set(v) {}
    },
  },
  watch: {
    /** 블로그 환경설정 미리보기 데이타 */
    previewBlogConfig: {
      handler: function(val, oldVal) {
        this.$store.dispatch('BlogConfig/FETCH_BLOG_CONFIG', val);
        this.$store.dispatch('BlogConfig/FETCH_PREVIEW_DATA', val);
      },
      deep: true,
    },
  },
  methods: {
    /** 블로그 환경설정 저장 */
    async onSubmit(values) {
      const confirm = await messageUtil.confirmSuccess('저장하시겠습니까?');
      if (!confirm) return;

      const headers = { 'Content-Type': 'multipart/form-data' };

      this.$http.put('/blogconfig', values, { headers })
      .then(res => {
        messageUtil.toastSuccess('저장되었습니다.');

        this.$store.dispatch('BlogConfig/FETCH_BLOG_CONFIG', res.data);
        this.$store.dispatch('BlogConfig/FETCH_PREVIEW_DATA', null);

        breadcrumbService.setPageTitle(this.pageTitle);
      });
    },
    /** 블로그 아바타 이미지 file input 값 변경 시 */
    onChangeAvatarImg(values) {
      if (isNotEmpty(values)) {
        this.avatarImg = values.name;
        this.avatarImgSize = values.size;
      }
    },
    /** 블로그 대표 이미지 file input 값 변경 시 */
    onChangeOgImg(values) {
      if (isNotEmpty(values)) {
        this.ogImg = values.name;
        this.ogImgSize = values.size;
      }
    },
    /** Cloudinary 파일 클릭 시 */
    onClickFile(file, key) {
      if ('avatar' === key) {
        this.$refs['blogConfigForm'].setFieldValue('avatarImg', file.public_id);
        this.$refs['blogConfigForm'].setFieldValue('avatarImgUrl', file.url);
        this.$refs['blogConfigForm'].setFieldValue('avatarImgSize', file.bytes);
      } else if ('og' === key) {
        this.$refs['blogConfigForm'].setFieldValue('ogImg', file.public_id);
        this.$refs['blogConfigForm'].setFieldValue('ogImgUrl', file.url);
        this.$refs['blogConfigForm'].setFieldValue('ogImgSize', file.bytes);
      }
    },
    /** Cloudinary 파일 목록 조회 시 */
    onListFile(files, key) {
      if ('avatar' === key) {
        this.ogFileList = [];
        this.avatarFileList = files;
      } else if ('og' === key) {
        this.avatarFileList = [];
        this.ogFileList = files;
      }
    },
  },
}
</script>

<style lang="scss" scoped>
@import '@/assets/scss/views/blog-config.scss';
</style>